
import { FaStar, FaArrowAltCircleLeft, FaTrashAlt, FaArrowUp, FaChevronCircleLeft } from 'react-icons/fa'
import React, { useEffect, useState } from 'react';
import { addToCart, removeFromCart } from '../actions/addTocart';
import { onSuccessBuy } from '../actions/payment';
import { useDispatch, useSelector } from 'react-redux';
import { Link } from 'react-router-dom';
import './Cart.css'
import NavBar from './NavBar';
import Paypal from './Paypal';
import Axios from 'axios';

function Cart(props) {

  const cart = useSelector(state => state.cart );
   
  const { cartItems, showSuccessMessage , showDeleteMessage} = cart;

  const productId = props.match.params.id;
  const qty = props.location.search ? Number(props.location.search.split("=")[1]) : 1;
  const dispatch = useDispatch();

  const [Total, setTotal] = useState(0)
  const [ShowTotal, setShowTotal] = useState(false)
  const [ShowSuccess, setShowSuccess] = useState(false)


  const removeFromCartHandler = (productId) => {
    dispatch(removeFromCart(productId));
    props.history.push('/cart');

  }
  useEffect(() => {
    if (productId) {
      dispatch(addToCart(productId,qty));
    }
  }, []);


const transactionSuccess = (data) => {
    dispatch(onSuccessBuy(data));
    props.history.push('/api/products');
  }

const transactionError = () => {
    console.log('Paypal error')
}

const transactionCanceled = () => {
    console.log('Transaction canceled')
}


  return   <div> <NavBar/>
  { showDeleteMessage &&
    <div className="alert alert-danger alert-dismissible fade show">
    <strong>Success!</strong> Item successfully removed.
    <button type="button" className="close" data-dismiss="alert">&times;</button>
</div>
}
{ showSuccessMessage &&
    <div className="alert alert-success alert-dismissible fade show">
    <strong>Success!</strong> Item successfully added to Cart.
    <button type="button" className="close" data-dismiss="alert">&times;</button>
</div>
}
  <div className="cart">
    <div className="cart-list">
      <ul className="cart-list-container">
        <li>
          <h3>
            Shopping Cart
          </h3>
          <Link to='/api/products' id="backarrow" ><p >
                                    <FaArrowAltCircleLeft className="card-text" color='babyblue' size={30} />
                 Back to Products
               </p>
               </Link>
          <div>
            Price
          </div>
        </li>
        {
          cartItems.length === 0 ?
            <div>
              Cart is empty
          </div>
            :
            cartItems.map(item =>
              <li>
                <div className="cart-image">
                  <img src={item.picture} alt="product" />
                </div>
                <div className="cart-name">
                  <div>
                    <Link to={"/product/" + item.product}>
                      {item.name}
                    </Link>

                  </div>
                  <div>
                    Qty:
                  <select value={item.qty} onChange={(e) => dispatch(addToCart(item.product, e.target.value))}>
                      {[...Array(item.qut).keys()].map(x =>
                        <option key={x + 1} value={x + 1}>{x + 1}</option>
                      )}
                    </select>
                    <button type="button" className="button" onClick={() => removeFromCartHandler(item.product)} >
                      Delete
                    </button>
                  </div>
                </div>
                <div className="cart-price">
                  ${item.price}
                </div>
              </li>
            )
        }
      </ul>

    </div>
    <div className="cart-action">
      <h3>
        Subtotal ( {cartItems.reduce((a, c) => a + c.qty , 0)} items)
        :
         $ {cartItems.reduce((a, c) => a + c.price * c.qty, 0)}
      </h3>
      <Paypal
         
      toPay={cartItems.reduce((a, c) => a + c.price * c.qty, 0)}
                    onSuccess={transactionSuccess}
                    transactionError={transactionError}
                    transactionCanceled={transactionCanceled}  
      />

    </div>

  </div>
  </div>
}





export default Cart